import { Line } from "react-chartjs-2";
import Chart from "chart.js/auto";
import {
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from "chart.js";
import { useContext } from "react";
import { DataSensor } from './SensorDataChart';
import stlyes from "./TempPage.module.css";

Chart.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function TempPage(props) {
  
  const { Temp2, xAxis, showStateTemp, hideTemp } = useContext(DataSensor);
  // console.log(`DataSensor${Temp}`);
  
  const graphData = {
    labels: xAxis,
    datasets: [
      {
        label: "Temperature",
        data: Temp2,
        borderColor: "rgb(255, 0, 0)",
        backgroundColor: 'rgba(255, 0, 0, 0.8)',
        fill: true,
        tension: 0.4
      },
      // {
      //   label: "B社",
      //   data: [60, 55, 57, 61, 75, 50],
      //   borderColor: "rgb(75, 100, 192)",
      // },
    ],
  };

  const options = {
    animation: false,
    plugins: {
      title: {
        display: true,
        text: "Temperature"
      },
      legend: { 
        position: "top"
      }
    },
    responsive: true,
    scales: {
      x: {
        stacked: false
      },
      y: {
        stacked: false,
        max: 100,
        min: 0
      },
      y1: {
        stacked: false,
        position: "right",
        max: 100,
        min: 0
      }
    }
  };


  if (showStateTemp) {
    return (
      <div>
        <Line
        height={500}
        width={800}
        data={graphData}
        id="chart-key"
        options={options}
      />
      <br></br>
      <p>
        <button className={stlyes.hidebutton} onClick={hideTemp}>Hide</button>
      </p>
      </div>
    );
  }

  return (
    <div>
      {/* <h3>กดเพื่อแสดงกราฟ</h3>
      <button onClick={showTemp}>Show</button> */}

    </div>
  );

}

export default TempPage;