import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import Header from "./components/Header";
import Footer from "./components/Footer";
import SensorChart from "./screens/SensorDataChart";
import Adjustment from "./screens/Adjustment";
import Axios from "axios";
import { useState } from "react";
import "./App.css";


const App = () => {

  const [DataAmount, setDataAmount] = useState([]);
  const [Data50, setData50] = useState([]);
  const [Inputdata, setInputdata] = useState();
  const [Command, setCommand] = useState(Boolean);
  const [Switch_1, setSwitch_1] = useState(Boolean);
  const [Switch_2, setSwitch_2] = useState(Boolean);
  const [Switch_3, setSwitch_3] = useState(Boolean);
  const [Switch_4, setSwitch_4] = useState(Boolean);
  const url = "//localhost:3000";

  const pathURL = window.location.href;
  const modifiedURL = pathURL.replace("http:", "");
  console.log(console.log(modifiedURL));

  const getDataAmount = () => {
    Axios.get(`${url}/dataAmount`).then((response) => {
      const dataamount = response.data[0]['COUNT(*)'];
      setDataAmount(dataamount);
      console.log(dataamount);
    });
  };

  const getData50 = () => {
    Axios.get(`${url}/data50`).then((response) => {
      const all50data = response.data.map(data => ({ id: data.id, temperature: data.temperature, voltage: data.voltage }));
      setData50(all50data);
      console.log("50 Data: ", all50data);
    });
  };
  
  const sentCommand = () => {
    Axios.post(`${url}/command`).then((response) => {
      const received_command = response.data.command;
      setCommand(!received_command);
      console.log("Sent Command: ", Command);
    });
  };

  const deleteData = () => {
    Axios.delete(`${url}/delete`).then((response) => {
    console.log("Delete: ", response);
    });
  };

  const sentSwitch_1 = () => {
    Axios.post(`${url}/switch_1`).then((response) => {
      const received_command = response.data.command;
      setSwitch_1(!received_command);
      console.log("Switch_1: ", Switch_1);
    });
  };

  const sentSwitch_2 = () => {
    Axios.post(`${url}/switch_2`).then((response) => {
      const received_command = response.data.command;
      setSwitch_2(!received_command);
      console.log("Switch_2: ", Switch_2);
    });
  };

  const sentSwitch_3 = () => {
    Axios.post(`${url}/switch_3`).then((response) => {
      const received_command = response.data.command;
      setSwitch_3(!received_command);
      console.log("Switch_3: ", Switch_3);
    });
  };

  const sentSwitch_4 = () => {
    Axios.post(`${url}/switch_4`).then((response) => {
      const received_command = response.data.command;
      setSwitch_4(!received_command);
      console.log("Switch_4: ", Switch_4);
    });
  };

  const show50data = (
    <table>
      <thead>
        <tr>
          <th>ID</th>
          <th>Temperature</th>
          <th>Voltage</th>
        </tr>
      </thead>
      <tbody>
        {Data50.map((data) => (
          <tr key={data.id}>
            <td>{data.id}</td>
            <td>{data.temperature}</td>
            <td>{data.voltage}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );

 function showSwitch_1 () {
  if (Switch_1 === true) {
    return (<button className="switch_on" onClick={sentSwitch_1}>Switch 1</button>)
  } else {
    return (<button className="switch_off" onClick={sentSwitch_1}>Switch 1</button>)
  }
 };

 function showSwitch_2 () {
  if (Switch_2 === true) {
    return (<button className="switch_on" onClick={sentSwitch_2}>Switch 2</button>)
  } else {
    return (<button className="switch_off" onClick={sentSwitch_2}>Switch 2</button>)
  }
 };

 function showSwitch_3 () {
  if (Switch_3 === true) {
    return (<button className="switch_on" onClick={sentSwitch_3}>Switch 3</button>)
  } else {
    return (<button className="switch_off" onClick={sentSwitch_3}>Switch 3</button>)
  }
 };

 function showSwitch_4 () {
  if (Switch_4 === true) {
    return (<button className="switch_on" onClick={sentSwitch_4}>Switch 4</button>)
  } else {
    return (<button className="switch_off" onClick={sentSwitch_4}>Switch 4</button>)
  }
 };

  console.log(Inputdata);

  const [Adjustmentdata, setAdjustmentdata] = useState(1);
  console.log(Adjustmentdata);

  return (
    <Router>
      <Header />
      <main className="py-3">
        <div>
            <p>Pathname: {modifiedURL}</p>
        </div>
        <Routes>
          <Route exact path="/" element={<SensorChart Adjustmentdata={Adjustmentdata} url={url}/>} />
        </Routes>
        <div>
          <div>
            <input onChange={(event) => setInputdata(event.target.value)} />
            <div className="input">{`Input Data : ${Inputdata}`}</div>
          </div>
          <div>
            <Adjustment Adjustmentdata={Adjustmentdata} setAdjustmentdata={setAdjustmentdata} />
          </div>
          <br></br>
          <div>
            <p>Total Data Count: {DataAmount}</p>
            <button onClick={getDataAmount}>Total Data Amount</button>
          </div>
          <br></br>
          <div>
            <button onClick={getData50}>Show Data 50 values</button>
          </div>
          <br></br>
          <div>
            <button onClick={deleteData}>Delete All Data</button>
          </div>
          <br></br>
          <div>
            <button onClick={sentCommand}>Sent Command</button>
            <p>Sent Command: {!Command ? 'LED ON': 'LED OFF'}</p>
          </div>

          <div className="graphArea">
            <div className="grid">
              <div className="box">
              {showSwitch_1()}
              <p>Switch 1: {!Switch_1 ? 'ON': 'OFF'}</p>
              </div>
              <div className="box">
              {showSwitch_2()}
              <p>Switch 2: {!Switch_2 ? 'ON': 'OFF'}</p>
              </div>
              <div className="box">
              {showSwitch_3()}
              <p>Switch 3: {!Switch_3 ? 'ON': 'OFF'}</p>
              </div>
              <div className="box">
              {showSwitch_4()}
              <p>Switch 4: {!Switch_4 ? 'ON': 'OFF'}</p>
              </div>
            </div>
          </div>

          <div className="tables">
            {show50data}
          </div>
        </div>
      </main>
      <Footer />
    </Router>
  );
  
};

export default App;

//set PORT=5000 && npm start
