import React, { useEffect, useState, useReducer }  from "react";
import TempPage from "./TempPage";
import VoltagePage from "./VoltagePage";
import stlyes from "./SensorDataChart.module.css";
// import styled from "styled-components";


const DataSensor = React.createContext();

function reducer(state, action) {
  if (action.type === 'show') {
    return true;
  }
  if (action.type === 'hide') {
    return false;
  }
  return state;
}

function SensorChart(props) {
  const { Adjustmentdata, url } = props;
  const [Temp, setTemp] = useState([0]);
  const [Voltage, setVoltage] = useState([0]);
  const [xAxis, setxAxis] = useState([0]);
  const [Temp2, setTemp2] = useState([0]);
  const [Voltage2, setVoltage2] = useState([0]);
 
  useEffect(() => {

    const url2 = `wss:${url}/receive`;
    const socket = new WebSocket(url2);



    socket.addEventListener('open', function (event) {
      console.log('Connected to WS Server');
    });

    socket.addEventListener('message', function (event) {
      let Realmessage = event;
      // console.log('Real Message from server ', Realmessage);
      let message = JSON.parse(event.data); // Parse the incoming data
      // console.log('Message from server ', message);
      let temperature = message.temperature;
      let voltage = message.voltage;
      // console.log(`Used Data, Temp: ${temp} Voltage: ${voltage}`);
      setTemp((prevData) => [...prevData, temperature].slice(-50)); // Append the new data to the existing data
      setVoltage((prevData) => [...prevData, voltage].slice(-50)); // Append the new data to the existing data
      setxAxis((prevData) => [...prevData, new Date().toLocaleTimeString()].slice(-50)); // Append the new data to the existing data
    });

    
    return () => {
      socket.close();
    };
  }, []);


  // const Grid = styled.div`
  //   margin: 0;
  //   padding: 0;
  //   display: Grid;
  //   row-gap: 20px;
  //   column-gap: 10px;
  //   grid-template-columns: 1fr 1fr 1fr;
  //   justify-content: center;
  //   align-items: center;
  //   text-align: center;
  // `;

//   const Box = styled.div`
//     width: 650px; /* Set the width of the frame */
//     height: Auto; /* Set the height of the frame */
//     border: 0.5px solid blue;
// `;

  useEffect(() => {
    let lastTemp = Temp[Temp.length-1]*Adjustmentdata;
    setTemp2((prevData) => [...prevData, lastTemp].slice(-50));
    let lastVoltage = Voltage[Voltage.length-1]*Adjustmentdata;
    setVoltage2((prevData) => [...prevData, lastVoltage].slice(-50));

}, [Temp]);


  const [showStateTemp, showDispatchTemp] = useReducer(reducer, null);
  
  function showTemp(event) {
    event.preventDefault();
    showDispatchTemp({ type: 'show' });
  }
  
  function hideTemp() {
    showDispatchTemp({ type: 'hide' });
  }
  
  const [showStateVoltage, showDispatchVoltage] = useReducer(reducer, null);

  function showVoltage(event) {
    event.preventDefault();
    showDispatchVoltage({ type: 'show' });
  }

  function hideVoltage() {
    showDispatchVoltage({ type: 'hide' });
  }

  return (
    <DataSensor.Provider value={{Temp2, Voltage2, xAxis, showStateTemp, showStateVoltage, hideTemp, hideVoltage}}>
      <div className={stlyes.button_space}>
        {showStateTemp ? (
          <button className={stlyes.hidebutton} onClick={hideTemp}>Hide Temperature</button>
        ) : (
          <button className={stlyes.showbutton} onClick={showTemp}>Show Temperature</button>
        )}
      </div>
      <div className={stlyes.button_space}>
        {showStateVoltage ? (
          <button className={stlyes.hidebutton} onClick={hideVoltage}>Hide Voltage</button>
        ) : (
          <button className={stlyes.showbutton} onClick={showVoltage}>Show Voltage</button>
        )}
      </div>

      <div className={stlyes.graphArea}>
        <div className={stlyes.grid}>
          <div className={stlyes.box}>
            <TempPage Adjustmentdata={Adjustmentdata} />
            <p>Temperature Value: {Temp2[Temp2.length-1]}</p>
          </div>
          <div className={stlyes.box}>
            <VoltagePage/>
            <p>Voltage Value: {Voltage2[Voltage2.length-1]}</p>
          </div>
          {/* <div className={stlyes.box}>
            <Graph/>
          </div> */}
        </div>
      </div>
    </DataSensor.Provider>

  );
};

export { DataSensor };
export default SensorChart;


