import { Line } from "react-chartjs-2";
import Chart from "chart.js/auto";
import {
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from "chart.js";
import { useContext } from "react";
import { DataSensor } from './SensorDataChart';
import stlyes from "./VoltagePage.module.css";

Chart.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function VoltagePage() {
  const { Voltage2, xAxis, showStateVoltage, hideVoltage } = useContext(DataSensor);
  // console.log(`DataSensor${Voltage}`);
  
  const graphData = {
    labels: xAxis,
    datasets: [
      {
        label: "Voltage",
        data: Voltage2,
        borderColor: "rgb(0, 0, 255)",
        backgroundColor: 'rgba(0, 0, 255, 0.8)',
        fill: true,
        tension: 0.4
      },
      // {
      //   label: "B社",
      //   data: [60, 55, 57, 61, 75, 50],
      //   borderColor: "rgb(75, 100, 192)",
      // },
    ],
  };

  const options = {
    animation: false,
    plugins: {
      title: {
        display: true,
        text: "Voltage"
      },
      legend: { 
        position: "top"
      }
    },
    responsive: true,
    scales: {
      x: {
        stacked: false
      },
      y: {
        stacked: false,
        max: 100,
        min: 0
      },
      y1: {
        stacked: false,
        position: "right",
        max: 100,
        min: 0
      }
    }
  };





  if (showStateVoltage) {
    return (
      <div>
        <Line
        height={500}
        width={800}
        data={graphData}
        id="chart-key"
        options={options}
      />
      <br></br>
      <p>
        <button className={stlyes.hidebutton} onClick={hideVoltage}>Hide</button>
      </p>
      </div>
    );
  }

  return (
    <div>
      {/* <h3>กดเพื่อแสดงกราฟ</h3>
      <button onClick={showTemp}>Show</button> */}

    </div>
  );

}

export default VoltagePage;