import stlyes from "./Adjustment.module.css";
import { useState } from "react";


function Adjustment(props) {

  const { Adjustmentdata, setAdjustmentdata } = props;

  return (
    <div>
      <input placeholder="value" onChange={(event) => setAdjustmentdata(event.target.value)} />
      <div className="input">{`Adjustment Data : ${Adjustmentdata}`}</div>
    </div>
  );

}

export default Adjustment;